<template>
    <div v-b-hover="hoverHandler">    
      <v-icon x-small  class="float-left"  style="cursor: pointer; margin-top: 6px; margin-right:5px" @click="borrar">mdi-format-line-spacing</v-icon>
      {{tarea}}
      <v-icon v-if="isHovered" class="float-right"  style="cursor: pointer;" @click="borrar">mdi-check</v-icon>
    </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
    name: 'Tarea',
    props: {
        tarea: String,
        indiceT: Number,
        indiceA: Number
    },
    data(){
        return{
            isHovered: false,
        }
    },
    methods:{
        hoverHandler (hovered){
            this.isHovered = hovered; 
        },
        borrar() {
            this.$store.commit('borrar_tarea', {indexA: this.indiceA, indexT: this.indexT})
        }
    }
}
</script>