import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lista_asignaturas: [],
  },
  mutations: {
    agregar_asignatura (state, a){
        if (a !== ''){
            state.lista_asignaturas.push({asignatura: a, tareas: []}); 
            localStorage.setItem('lista_asignaturas', JSON.stringify(state.lista_asignaturas));
        }
    },
    borrar_asignatura(state, index) {
        state.lista_asignaturas.splice(index,1); 
        localStorage.setItem('lista_asignaturas', JSON.stringify(state.lista_asignaturas));
    },
    agregar_tarea(state, obj){
        state.lista_asignaturas[obj.asig_index].tareas.push(obj.tarea); 
        localStorage.setItem('lista_asignaturas', JSON.stringify(state.lista_asignaturas));
    },
    borrar_tarea(state, obj){
        state.lista_asignaturas[obj.indexA].tareas.splice(obj.indexT,1); 
        localStorage.setItem('lista_asignaturas', JSON.stringify(state.lista_asignaturas));
    },
    guardar_lista_desde_local (state, local){
      state.lista_asignaturas = local; 
    },
    guardar (state){
      localStorage.setItem('lista_asignaturas', JSON.stringify(state.lista_asignaturas));
    }
  },
  actions: {
  },
  modules: {
  }
})
