<template>
    <div class="card" style="width: 15rem; margin:7px ">
    <div class="card-body">
      <h5 class="card-title">{{title}}
      <v-icon class="float-right"   v-b-modal="'id' + this.indice" >mdi-delete</v-icon>
      </h5>
      <b-modal :id="'id' + this.indice" :title="'¿Deseas eliminar ' + this.title + ' y sus tareas?'" hide-header>  
        <h5>¿Deseas eliminar <b class="text-danger">{{this.title}}</b>  y sus tareas?</h5>
        <template v-slot:modal-footer="{cancel}">
        <b-button variant="secondary" @click="cancel()">Cancelar</b-button>
        <b-button variant="danger" @click="borrar_asignatura">Eliminar</b-button>
        </template>
      </b-modal>
      </div>
      
      <draggable v-model="lista_asignaturas[indice].tareas"   @end="guardar">
        <ul class="list-group list-group-flush" v-for="(t, index) of lista_asignaturas[indice].tareas">
        <li class="list-group-item float-left">
          <Tarea v-bind:tarea="t" v-bind:indiceT="index" v-bind:indiceA="indice"></Tarea>
        </li>
        </ul>     
      </draggable> 

      <div class="input-group ">
        <input type="text" class="form-control" v-model="tarea"  @keyup.enter="agregar_tarea" placeholder="Nueva tarea"  aria-describedby="basic-addon2">
        <div class="input-group-append">
        <button class="btn btn-outline-primary" type="button" @click="agregar_tarea">Añadir</button>
        </div>
    </div>

    <!-- <b-modal v-model="showreturn" size="sm" hide-header hide-footer hide-header-close hide-backdrop >
      <div class="text-center">
        <p class="text-success">Tarea completada! 
        <a href="#" class="text-danger" @click="recuperar_tarea_borrada">Deshacer cambios.</a></p>
      </div>
    </b-modal> -->


    </div>  

</template>

<script>
import Tarea from './Tarea.vue'
import draggable from 'vuedraggable'
import { mapState } from 'vuex';


  export default {
    name: 'Asignatura',
    props: {
        title: String,
        indice: Number
    },
    data() {
        return {
            tarea:'',
            // tareabuffer:'',
            // showreturn: false, 
        }
    },
    computed:{
      ...mapState(['lista_asignaturas']),
    },
    components:{
      Tarea,
      draggable
    },
    methods: {
        borrar_asignatura(){
            this.$store.commit('borrar_asignatura', this.indice);
        },
        agregar_tarea(){
            this.$store.commit('agregar_tarea',{asig_index: this.indice, tarea: this.tarea})
            this.tarea =''
        },
        // recuperar_tarea_borrada (){
        //   this.showreturn = false; 
        //   this.$emit('agregar_tarea_child', this.indice, this.tareabuffer);
        // },
        guardar(){
          this.$store.commit('guardar')
        }
    },
};
</script>
