<template>
  <div id="app">
    <div class="container-fluid p-5">
        <h3>
          {{titulo}}
          <v-icon right>mdi-book-open-variant</v-icon>
                <div class="float-right">
                    <b-button  v-b-modal.modal-nueva-asig>+</b-button>                    
                </div>
        </h3>
            <div class="row">
                <div v-for="(a, index) of lista_asignaturas" >
                    <Asignatura v-bind:title="a.asignatura" v-bind:tareas="a.tareas" v-bind:indice="index"></Asignatura>
                </div>  
            </div>
        </div>
        <!-- Código de los pop-ups -->
        <b-modal id="modal-nueva-asig" title="Nueva asignatura">  
                    <form>
                      <b-form-group invalid-feedback="Name is required">
                        <b-form-input v-model="asignatura"></b-form-input>
                      </b-form-group>
                    </form>
                    <template v-slot:modal-footer="{ cancel }">
                    <b-button variant="secondary" @click="cancel()">Cancelar</b-button>
                    <b-button variant="primary" @click="agregar_asignatura">Añadir</b-button>
                    </template>
        </b-modal>
  </div>
</template>


<script>
import Asignatura from './components/Asignatura.vue'
import {mapState, mapMutations} from 'vuex'; 
export default {
    data() {
      return {
        titulo: 'Mis asignaturas', 
        asignatura: ''
      }
    },
    computed:{
      ...mapState(['lista_asignaturas']),
    },
    components: {
      Asignatura
    },
    methods: {
      agregar_asignatura(){
        this.$store.commit('agregar_asignatura', this.asignatura);
        this.asignatura ='';
      },
    },
    mounted: function(){
        let local = JSON.parse(localStorage.getItem('lista_asignaturas')); 
        if (local == null){
            local = []; 
        }
        this.$store.commit('guardar_lista_desde_local', local);
    }
}
</script>